
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import Scroll from 'assembly/scroll/scroll'
import localStore from 'util/localStore'

export default {
components: {
XHeader,
Confirm,
Scroll,
Popup
},
data () {
return {
payPop: false,
fmsClientOrderVO: {},
orderDetailId: '',
pName: '',
orderNo: '',
orderStatus: 0,
payStatus: 0,
statusText: '',
statusIcon: '',
products: [],
promotions: [],
clientGifts: [],
reFund: false,
aSale: false,
canReturn: false, // 是否能一键加回购物车
balance: 0,
creditAvailable: 0,
totalNum: 0,
redirect: '', // 从哪个页面来
downTime: 0 
}
},
methods: {
goBack () {
this.$router.push({
path: this.redirect ? '/' + this.redirect : '/order'
})
},
toRecharge () {
this.$router.push({
path: '/recharge',
query: {
amount: Math.round((this.creditAvailable > 1 ? this.fmsClientOrderVO.mustPay - this.balance - this.creditAvailable : this.fmsClientOrderVO.mustPay - this.balance) * 100) / 100,
orderNo: this.orderNo
}
})
},
toPay () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.balance = this.formatePrice(res.data.model.userAccount.balanceAvailable)
this.creditAvailable = this.formatePrice(res.data.model.userAccount.creditAvailable)
this.payPop = true
})
},
formatePrice (num) {
return num / 100
},
confirmPay () {
this._submit()
this.payPop = false
},
closePayPop () {
if (this.balance + this.creditAvailable < this.fmsClientOrderVO.mustPay) {
this.$uweb.trackEvent('120', '订单详情页-余额不足提示点击关闭按钮', '0')
} else {
this.$uweb.trackEvent('118', '订单详情页-支付订单弹窗点击关闭按钮', '0')
}
this.payPop = false
},
orderRefund () {
this.reFund = true
},
onConfirm () {
let data = {
orderNo: this.orderNo
}
this.$tupHttp(this, 'front/order/orderRefund', 'POST', data).then(res => {
this._getDetail()
})
},
afterSale (orderDetailId, pName) {
this.aSale = true
this.orderDetailId = orderDetailId
this.pName = pName
},
onASConfirm () {
this.$router.push({
path: '/afterSaleType',
query: {
orderNo: this.orderNo,
lineno: this.orderDetailId
}
})
},
/*
待付款 payStatus=0 && orderStatus in (1,2)
待发货 payStatus in (1,2) && orderStatus in (1,2,3,4,5)
待收货 orderStatus = 6
已取消 orderStatus = 7
已完成 orderStatus = 8
*/
setStatus () {
switch (this.orderStatus) {
case 1:
case 2:
if (this.payStatus === 0) {
this.statusText = '订单待付款'
this.statusIcon = 'icon-daifukuan'
} else if (this.payStatus === 1 || this.payStatus === 2) {
this.statusText = '订单待发货'
this.statusIcon = 'icon-daifahuo1'
}
break
case 3:
case 4:
case 5:
this.statusText = '订单待发货'
this.statusIcon = 'icon-daifahuo1'
break
case 6:
this.statusText = '订单待收货'
this.statusIcon = 'icon-daishouhuo'
break
case 7:
this.statusText = '订单已取消'
this.statusIcon = 'icon-yiquxiao1'
break
case 8:
this.statusText = '订单已完成'
this.statusIcon = 'icon-yiwancheng'
break
}
},
orderReBuy () {
this.$tupHttp(this, 'front/shoppingCart/restoreFromOrder?orderNo=' + this.orderNo, 'POST').then(rs => {
this.$tupHttp(this, 'front/shoppingCart/load').then(res => { // 获取线上购物车数据
let data = res.data.model
if (data) {
localStore.set('TUP_cart', {
'promotion': data.promotions || [],
'product': data.products || [],
'giftProducts': data.giftProducts || [],
'needGiftProduct': 1 // 是否需要指定赠送，默认1是
})
}
if (this.redirect) {
this.$router.push({
path: '/shoppingCart'
})
} else {
this.$vux.toast.text('产品已加入购物车，请返回购物车查看')
}
})
})
},
formatTime () {
let downTime = this.downTime
let timer = setInterval(() => {
if (downTime <= 0) {
clearInterval(timer)
this.downTime = 0
return
}
let hours = Math.floor(downTime / 1000 / 60 / 60)
let minutes = Math.floor(downTime / 1000 / 60 - 60 * hours)
let seconds = Math.floor(downTime / 1000 - 60 * 60 * hours - 60 * minutes)
this.downTime = hours + '时' + minutes + '分' + seconds + '秒'
downTime = downTime - 1000
}, 980)
},
_submit () {
this.$tupHttp(this, 'front/shoppingCart/pay', 'POST', {
orderNo: this.orderNo
}).then(res => {
this.$vux.toast.text('付款成功')
setTimeout(() => {
this.$router.go(0)
}, 200)
})
},
_getDetail () {
this.$tupHttp(this, 'front/order/getClientOrderDetail/' + this.orderNo).then(res => {
this.fmsClientOrderVO = res.data.model.fmsClientOrderVO
this.orderStatus = this.fmsClientOrderVO.orderStatus
this.payStatus = this.fmsClientOrderVO.payStatus
this.products = res.data.model.products
this.promotions = res.data.model.promotions
this.clientGifts = res.data.model.clientGifts
if (this.orderStatus === 7 && new Date(this.fmsClientOrderVO.createtime).format('yyyy-MM-dd') === new Date().format('yyyy-MM-dd') && this.fmsClientOrderVO.returnReason !== '8') {

this.canReturn = true
}
this.setStatus()

for (let i = 0; i < this.promotions.length; i++) {
for (let j = 0; j < this.promotions[i].productList.length; j++) {
this.totalNum += this.promotions[i].productList[j].shopCartNum
}
for (let j = 0; j < this.promotions[i].giftList.length; j++) {
this.totalNum += this.promotions[i].giftList[j].shopCartNum
}
}
for (let i = 0; i < this.products.length; i++) {
this.totalNum += this.products[i].shopCartNum
}




})
}
},
created () {
this.orderNo = this.$route.query.orderNo
this.redirect = this.$route.query.redirect
if (this.orderNo) {
this._getDetail()
}
}
}



